// type  1  0
export function checkUpdate(update_info, type = 0) {
	return new Promise((reolve, reject) => {
		// 
		plus.runtime.getProperty(plus.runtime.appid, async (inf) => {
			console.log('', inf.version);
			console.log('', update_info.version);
			// 
			let need_update = await compareVersion(inf.version, update_info.version);
			//
			if (!need_update) {
				return reolve({
					msg: ""
				})
			}
			//
			if (/\.wgt$/.test(update_info.now_url) && update_info.silent == 1) {
				console.log("");
				if (type == 1) {
					return reolve({
						msg: ""
					})
				}
				startSilentUpdate(update_info.now_url); //
				return reolve({
					msg: ""
				})
			}

			//
			let update_ignore_version = uni.getStorageSync("update_ignore") || "0.0.0";
			console.log("", update_info.force);
			if (type === 0 && update_ignore_version == update_info.version && update_info.force ===
				0) {
				console.log("");
				return reolve({
					msg: ''
				})
			}

			//
			uni.navigateTo({
				url: "/pages/versionUpdate/versionUpdate?updata_info=" + JSON.stringify(
					update_info),
				animationType: "fade-in"
			})
		});
	})
}

// 
function compareVersion(ov, nv) {
	return new Promise((reolve, reject) => {
		if (!ov || !nv || ov == "" || nv == "") {
			return reolve(false);
		}
		let b = false;
		let ova = ov.split(".", 4);
		let nva = nv.split(".", 4);
		for (let i = 0; i < ova.length && i < nva.length; i++) {
			let so = ova[i],
				no = parseInt(so),
				sn = nva[i],
				nn = parseInt(sn);
			if (nn > no || sn.length > so.length) {
				return reolve(true);
			} else if (nn < no) {
				return reolve(false);

			}
		}
		if (nva.length > ova.length && 0 == nv.indexOf(ov)) {
			return reolve(true);
		} else {
			return reolve(false);
		}
	})
}

// 
function startSilentUpdate(url) {
	let options = {
		method: "get"
	};
	console.log("", url);
	let dtask = plus.downloader.createDownload(url, options);
	dtask.addEventListener("statechanged", function(task, status) {
		if (status === null) {} else if (status == 200) {
			// 
			switch (task.state) {
				case 4:
					console.log("", task.filename);
					installWgt(task.filename); //   
					break;
			}
		}
	});
	dtask.start();
}

// 
function installWgt(path) {
	plus.runtime.install(path);
}
